import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  /*  */
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  base: 'authority/permissions',
  routes: [
    {path: '/permission', component: resolve => require(['components/authority/permissions/permissions.vue'], resolve), meta: {nav: ['', '']}},
    {path: '/staff', component: resolve => require(['components/authority/permissions/index.vue'], resolve), meta: {nav: ['', '']}},
    {path: '/role', component: resolve => require(['components/authority/permissions/role.vue'], resolve), meta: {nav: ['', '']}}
  ]
})



